document.addEventListener('DOMContentLoaded', 
iniciarApp)

function iniciarApp() {

    menu();
    carousel();
    darkTheme();
    efectosGsap();
}

function menu() {
    const hamburguer = document.getElementById('hamburguer');
    const lista = document.querySelector('.lista');
    
    hamburguer.addEventListener('click', () => {lista.classList.toggle('mostrar')});
}

function carousel() {
    new Glider(document.querySelector('.cards--items'), {
        slidesToShow: 1,
        dots: '.proyectos--indicadores',
        draggable: true,
      });
}

function darkTheme() {
    const chk = document.getElementById('chk');
    chk.addEventListener('change', ()=>{
        document.body.classList.toggle('dark');
    })
}

function efectosGsap() {
    const proyectos = document.querySelector('.proyectos');
    gsap.to(proyectos, { duration: 2.1, ease: "power2.in", y: 0 });

    const first = document.querySelector('.first');
    const second = document.querySelector('.second');
    const third = document.querySelector('.third');

    TweenMax.to(first, 1.5, {
        delay: .5,
        top: "-100%",
        ease: Expo.easeInOut
    });
    TweenMax.to(second, 1.5, {
        delay: .7,
        top: "-100%",
        ease: Expo.easeInOut
    });TweenMax.to(third, 1.5, {
        delay: .9,
        top: "-100%",
        ease: Expo.easeInOut
    });


    const tituloApolo = document.querySelector('.front--div--text');

    TweenMax.to(tituloApolo, 1.5,{
        delay: 3,
        Y: "100%",
        ease: Expo.easeInOut
    });

}